import React from "react";

const DisplayPractical = (props) => {
    const {practical} = props;

    return (
        <div>
            <h3>Practical</h3>
            {(practical.map(function(work) {
                    return (
                        <div key={work.id}>
                            <h2>Company: {work.company}</h2>
                            <p>Title: {work.title}</p>
                            <p>City: {work.city}</p>
                            <p>Start Date: {work.from}</p>
                            <p>End Date: {work.to}</p>
                        </div>
                    )
                })
            )}
        </div>
    )
}

export default DisplayPractical;