import { render } from "@testing-library/react";
import React, { Component } from "react";



const General = (props) => {
    const {user, updatePersonal} = props;
    return (
        <div id="general" className="main-section">
            <input onChange={updatePersonal} type="text" name="firstName" placeholder="First Name" value={user.firstName}/>
            <input onChange={updatePersonal} type="text" name="lastName" placeholder="Last Name" value={user.lastName}/>
            <input onChange={updatePersonal} type="text" name="email" placeholder="Email" value={user.email}/>
            <input onChange={updatePersonal} type="text" name="number" placeholder="Phone Number" value={user.number}/>
        </div>
    );
}

export default General;