import React from "react";

const DisplayPersonal = (props) => {
    const {user} = props;

    return (
        <div>
            <h3>Personal</h3>
            <p>First Name: {user.firstName}</p>
            <p>Last Name: {user.lastName}</p>
            <p>Email: {user.email}</p>
            <p>Phone Number: {user.number}</p>
        </div>
    )
}

export default DisplayPersonal;