import React from "react";

const Education = (props) => {
    const { education, editEducation, addEducation, removeEducation } = props;
    return (
        <div id="education" className="main-section">
            {education.map((edu) => {
                return (
                    <div key={edu.id}>
                        <input 
                            type="text" 
                            name="uni" 
                            placeholder="University Name" 
                            id={edu.id} 
                            value={edu.uni}
                            onChange={editEducation}
                        />
                        <input 
                            type="text" 
                            name="degree" 
                            placeholder="Degree" 
                            id={edu.id} 
                            value={edu.degree}
                            onChange={editEducation}
                        />
                        <input 
                            type="text" 
                            name="focus" 
                            placeholder="Focus" 
                            id={edu.id} 
                            value={edu.focus}
                            onChange={editEducation}
                        />
                        <input 
                            type="text" 
                            name="city" 
                            placeholder="City" 
                            id={edu.id} 
                            value={edu.city}
                            onChange={editEducation}
                        />
                        <input 
                            type="text" 
                            name="year" 
                            placeholder="Year Graduated" 
                            id={edu.id} 
                            value={edu.year}
                            onChange={editEducation}
                        />
                        <button id={edu.id} onClick={removeEducation}>X</button>
                    </div>
                )
            })}
            <button onClick={addEducation}>+</button>
        </div>
    );
}

export default Education;