import './styles/App.css';
import React, { Component } from "react";
import Form from './components/form';
import uniqid from "uniqid";
import Display from './components/display';

class App extends Component {
  constructor() {
    super()

    this.state = {
      user: {
        firstName: "",
        lastName: "",
        email: "",
        number: "",
      },
      education: [
        {
          uni: "",
          city: "",
          degree: "",
          focus: "",
          year: "",
          id: uniqid(),
        }
      ],
      practical: [
        {
          title: "",
          company: "",
          city: "",
          from: "",
          to: "",
          id: uniqid(),
        }
      ],
    }
    this.updatePersonal = this.updatePersonal.bind(this);
    this.editEducation = this.editEducation.bind(this);
    this.addEducation = this.addEducation.bind(this);
    this.removeEducation = this.removeEducation.bind(this);
    this.editPractical = this.editPractical.bind(this);
    this.addPractical = this.addPractical.bind(this);
    this.removePractical = this.removePractical.bind(this);
  }

  updatePersonal = (e) => {
    const { name, value} = e.target;
    console.log(e.target);

    this.setState(prevState =>{
      return{
        user: {
          ...prevState.user,
          [name]: value,
        }
      }
    })
  }

  editEducation = (e) => {
    const { name, value, id} = e.target;

    this.setState(prevState => {
      const newEdu = prevState.education.map((edu) => {
        if (edu.id === id) {
          return {
            ...edu,
            [name]: value,
          }
        }
        else {
          return {
            ...edu,
          }
        }
      })
      return {
        education: newEdu,
      }
    })
  }

  addEducation = () => {
    this.setState(prevState => {
      const newEducation = prevState.education.concat(
        {
          uni: "",
          city: "",
          degree: "",
          focus: "",
          year: "",
          id: uniqid(),
        }
      );
      return {
        education: newEducation,
      }
    })
  }

  removeEducation = (e) => {
    const {id} = e.target;
    this.setState(prevState => {
      const newEducation = prevState.education.filter((edu) => {
        return edu.id !== id;
      })
      return {
        education: newEducation,
      }
    })
  }

  editPractical = (e) => {
    const { name, value, id} = e.target;

    this.setState(prevState =>{
      const newPractical = prevState.practical.map((job) => {
        if (job.id === id) {
          return {
            ...job,
            [name]: value,
          }
        }
        else {
          return {
            ...job,
          }
        }
      })
      return {
        practical: newPractical,
      }
    })
  }

  addPractical = (newEdu) => {
    this.setState(prevState => {
      const newPractical = prevState.practical.concat(
        {
          title: "",
          company: "",
          city: "",
          from: "",
          to: "",
          id: uniqid(),
        }
      );
      return {
        practical: newPractical,
      }
    })
  }

  removePractical = (e) => {
    const {id} = e.target;
    this.setState(prevState => {
      const newPractical = prevState.practical.filter((job) => {
        return job.id !== id;
      })
      return {
        practical: newPractical,
      }
    })
  }

  render() {
    const {user, education, practical } = this.state;
    return (
      <div>
        <Form
            user={user}
            education={education}
            practical={practical}
            updatePersonal={this.updatePersonal}
            editEducation={this.editEducation}
            addEducation={this.addEducation}
            removeEducation={this.removeEducation}
            editPractical={this.editPractical}
            addPractical={this.addPractical}
            removePractical={this.removePractical}
        />
        <Display
          user={user}
          education={education}
          practical={practical}
        />
      </div>
    )
  }
}

export default App;
