import React from "react";

const DisplayEducation = (props) => {
    const {education} = props;

    return (
        <div>
            <h3>Education</h3>
            {(education.map(function(school) {
                    return (
                        <div key={school.id}>
                            <h2>University: {school.uni}</h2>
                            <p>Degree: {school.degree}</p>
                            <p>Focus: {school.focus}</p>
                            <p>City: {school.city}</p>
                            <p>Year Graduated: {school.year}</p>
                        </div>
                    )
                })
            )}
        </div>
    )
}

export default DisplayEducation;