import React from "react";

const Practical = (props) => {
    const {practical, editPractical, addPractical, removePractical} = props;
    return (
        <div id="practical" className="main-section">
            {practical.map((job) => {
                return (
                    <div key={job.id}>
                        <input 
                            type="text" 
                            name="company" 
                            placeholder="Company Name" 
                            id={job.id} 
                            value={job.company}
                            onChange={editPractical}
                        />
                        <input 
                            type="text" 
                            name="title" 
                            placeholder="Job Title" 
                            id={job.id} 
                            value={job.title}
                            onChange={editPractical}
                        />
                        <input 
                            type="text" 
                            name="city" 
                            placeholder="City" 
                            id={job.id} 
                            value={job.city}
                            onChange={editPractical}
                        />
                        <input 
                            type="text" 
                            name="from" 
                            placeholder="Start Date" 
                            id={job.id} 
                            value={job.from}
                            onChange={editPractical}
                        />
                        <input 
                            type="text" 
                            name="to" 
                            placeholder="End Date" 
                            id={job.id} 
                            value={job.to}
                            onChange={editPractical}
                        />
                        <button id={job.id} onClick={removePractical}>X</button>
                    </div>
                )
            })}
            <button onClick={addPractical}>+</button>
        </div>
    );
}

export default Practical;